// finance action
import {Action} from '@ngrx/store';

export const TRY_FETCH_FINANCE = 'TRY_FETCH_FINANCE';
export const SET_FINANCE = 'SET_FINANCE';
export const FINANCE_EDIT = 'FINANCE_EDIT';
export const UPLOAD_FINANCE_IMAGES = 'UPLOAD_FINANCE_IMAGES';

// classes for finance
export class TryFetchFinance implements Action {
  readonly type = TRY_FETCH_FINANCE;
}

export class SetFinance implements Action {
  readonly type = SET_FINANCE;

  constructor(public payload: {}) {
  }
}

export class UploadFinanceImages {
  readonly type = UPLOAD_FINANCE_IMAGES;

  constructor(public payload: any) {
  }
}

export class FinanceEdit implements Action {
  readonly type = FINANCE_EDIT;

  constructor(public payload: {}) {
  }
}

export type FinanceActions =
  FinanceEdit |
  UploadFinanceImages |
  SetFinance |
  TryFetchFinance;
