import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {map, switchMap} from 'rxjs/operators';
import {HttpClient, HttpResponse} from '@angular/common/http';
import * as endPoints from '../../../../shared/serverEndpoints';
import * as FinanceActions from './finance.actions';

@Injectable()
export class FinanceEffects {
  constructor(public action: Actions, public http: HttpClient) {
  }

// effects for finance
  @Effect()
  editFinance = this.action.pipe(
    ofType(FinanceActions.FINANCE_EDIT),
    map((payload: FinanceActions.FinanceEdit) => {
      return payload.payload;
    }),
    switchMap((data) => {
      return this.http.post(endPoints.editfinance, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: FinanceActions.SET_FINANCE,
            payload: response.body
          }
        ];
      }
    })
  );


  @Effect()
  uploadFinanceImages = this.action.pipe(
    ofType(FinanceActions.UPLOAD_FINANCE_IMAGES),
    map((data: FinanceActions.UploadFinanceImages) => {
      return data.payload;
    }), switchMap((data: { registrationCinImage: File, registrationGstImage: File, registrationMsmeImage: File, registrationRcImage: File }) => {
      return this.http.post(endPoints.uploadfinanceImages, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: FinanceActions.SET_FINANCE,
            payload: response.body
          }
        ];
      }
    })
  );

  @Effect()
  fetchFinance = this.action.pipe(
    ofType(FinanceActions.TRY_FETCH_FINANCE),
    switchMap(() => {
      return this.http.get(endPoints.fetchfinance, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: FinanceActions.SET_FINANCE,
            payload: response.body
          }
        ];
      }
    })
  );
}
