import {FinanceModel} from '../finance.model';
import {FinanceActions} from './finance.actions';
import {AppState} from "../../../../store/app.reducer";

export interface FinanceState extends AppState {
  finance: State;
}

export interface State {
  finance: FinanceModel;
}

const initialState: State = {
  finance: new FinanceModel('', '', '', '', '', '', '', '', '', '', {
    permanentAddress: {
      address: '',
      pincode: '',
      phoneNo: ''
    }
  }, {
    profileRegistrar: '', name: '', mobile: '', email: '', pan: '', address: {
      address: '',
      phoneNo: '',
      pincode: ''
    }, bankDetails: {
      bankAccountNo: '',
      bankAddress: '', bankIfsc: '', bankName: ''
    }
  }, '', '', '')
};

export function FinanceReducer(state = initialState, action: FinanceActions) {
  switch (action.type) {
    case 'SET_FINANCE': {
      return {
        ...state,
        finance: action.payload
      };
    }
    default: {
      return {
        ...state
      };
    }

  }
}
